.Home-page {
    .Section {
        width: 100%;
        display: flex;
        flex-direction: column;
        background-color: #f7f3f5;

        &.odd {
            background-color: white;

            .subtitle {
                color: #262620;;
            }
        } 
    }

    .Header {
        width: 100%; 
        height: 300px;
        position: relative;
        color: white;
    }

    .Header > .subtitle {
        margin-left: 100px;
        margin-bottom: 100px;
        margin-top: 100px;
        font-size: 28px;
        font-family: Futura PT Book, sans-serif;
        position: absolute;
    }

    .Projects {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        >.Preview {
            width: 33.333333333%;
            padding-bottom:  20%;
        }
    }

    .fixed-background {
        background-image: url('../../assets/images/backgrounds/city.jpg');
        background-position: bottom center;
        background-size: cover;
        background-attachment: fixed;

        &:before {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-color: black;
            opacity: 0.25;
            content: '';
        }
    }
}