

/**
 * ----------------------------------------
 * slide-in-bottom
 * ----------------------------------------
 */
@keyframes slide-in-bottom {
  0% {
    transform: translateY(70px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * slide-down-top
 * ----------------------------------------
 */
@keyframes slide-in-top {
  0% {
    transform: translateY(-25px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * slide-down-top
 * ----------------------------------------
 */
 @keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/**
 * ----------------------------------------
 * kenburns-top
 * ----------------------------------------
 */
@keyframes kenburns-top {
  0% {
    transform: scale(1) translateY(0);
    transform-origin: 50% 16%;
  }
  100% {
    transform: scale(1.2) translateY(-10px);
    transform-origin: top;
  }
}

/**
 * ----------------------------------------
 * bouncing
 * ----------------------------------------
 */
@keyframes bouncing {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0);
  }
}



.App-banner {
  width: 100%;
  position: relative;
  overflow: hidden;

  .Header-text {
    padding: calc(100vh * 0.3) 100px;
    display: flex;
    flex-direction: column;
    position: absolute;
    max-width: 70%;
  }

  .background {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .title-large {
    font-size: 55px;
    font-weight: 900;
    font-family: Futura PT Book, sans-serif;
  }

  .subtitle {
    font-size: 25px;
    font-weight: 900;
    margin: 0;
  }

  .scroll-down {
    width: 100%;
    height: 150px;
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #bb550b;
    transition: all 0.2 ease;

     > .scroll-down-arrow {
      width: 50px;
      height: 50px;
      background-image: url('../../assets/images/icons/scrolldown.png');
      background-size: 100%;
      background-repeat: no-repeat;
      opacity: 0.75;
     }

     > p {
      text-shadow: 3px 3px 5px rgba(187,85,11,0.3);
     }
  }



  .slide-in {
    animation: slide-in-bottom 2.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 250ms;
  }

  .fade-in {
    animation: fade-in 3.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 400ms;
  }

  .kenburns-top {
    animation: kenburns-top 3s ease-out 0.5s forwards;
  }

  .bouncing {
    animation: bouncing 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 1s infinite;
  }
}